import {createStore, applyMiddleware} from 'redux'
import thunkMiddleware from 'redux-thunk'
let initState = {
    socket:null
}
function netWork(state=initState,action)
{
    switch(action.type){
        case 'INIT_SCOKET':
        
            return Object.assign({},state,{socket:action.socket})

        default:
        return state
    }


}


// function changeCount(state=initState,action){

//     switch (action.type){
//         case 'ADD':
//             let count = state.count;
//             return Object.assign({},state,{count:count+action.count})
//         default:
//          return state
//     }
// }

const store = createStore(netWork,{},applyMiddleware(thunkMiddleware));
export default store;


