

 class HMWebsocket{
    constructor(url,dispatch)
    {
        this.url=url;
        this.dispatch = dispatch;
        this.socket = null;
        this.connectToHost();
    }
    connectToHost()
    {
        this.socket = new WebSocket(this.url);
        this.socket.onOpen = function(){alert('opened')}
        return this.socket
    }
  
    setObserver(onMessage,onOpen,onError,onClose)
    {
        if(!this.socket)
            this.socket = this.connectToHost();
        this.socket.onOpen = onOpen;
        this.socket.onClose = onClose;
        this.socket.onMessage = onMessage
    }



    sendMessage(msg)
    {
        this.socket.send(msg);
    }
}
export default HMWebsocket;


