import HMWebsocket from '../api/HMWebsocket'

export function setWebscoket(socket)
{
    return{
        type:"INIT_SCOKET",
        socket:socket
    }
}


export function createWebscoket(url)
{
    return function(dispatch,getState)
    {
        let socket = new HMWebsocket(url,dispatch);
        console.log(socket)
        return dispatch(setWebscoket(socket));
    }
}




