import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import {Provider ,connect} from 'react-redux';
import store from './store/reducers'
import {createWebscoket} from './store/action'
import {bindActionCreators} from 'redux'


class SubApp extends Component {


  componentDidMount()
  {
    console.log(this.props)
    this.props.createWebscoket('ws://127.0.0.1:8080/dis/')
    console.log(this)
  }
  render() {
   
    return (
      
      <div className="App">

        111
      </div>
      
    );
  }
}
function mapDispatchToProps(dispatch){

return bindActionCreators({createWebscoket},dispatch)

// {
//   buildWebscoket:dispatch(actions.createWebscoket('ws://127.0.0.1:8000/dis/'))

// }

}


const SubAppContainer = connect(null,mapDispatchToProps)(SubApp)

class App extends React.Component{


  render(){

    return(
      <Provider store = {store}>
      <SubAppContainer/>
      </Provider>

    )
  }
}


export default App;



